
.button-container {
  margin-top: 24px;
}

.word-container {
  display: flex;
  height: 52px;
}

.word-container > * + * {
  margin-left: 8px;
}

.letter-container {
  height: 52px;
  position: relative;
  width: 52px;
}

.letter {
  align-items: center;
  box-sizing: border-box;
  display: flex;
  font-size: 1.5rem;
  font-weight: bold;
  height: 52px;
  justify-content: center;
  line-height: 2rem;
  position: absolute;
  user-select: none;
  width: 52px;
}

.guess {
  background-color: #fff;
  border: 2px solid #878a8c;
}

.result {
  color: #fff;
}

.result-full {
  background-color: #6aaa64;
}

.result-partial {
  background-color: #c9b458;
}

.result-none {
  background-color: #787c7e;
}
